body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	box-sizing: border-box;
	font-family: 'Vazir' !important;
}

@font-face {
    font-family: Vazir;
    src: url('/ot/fonts/Vazir.eot'),url('/fonts/Vazir.eot');
    src: url('/ot/fonts/Vazir.eot?#iefix') format('embedded-opentype'),
         url('/ot/fonts/Vazir.woff2') format('woff2'),url('/fonts/Vazir.woff2') format('woff2'),
         url('/ot/fonts/Vazir.woff') format('woff'),url('/fonts/Vazir.woff') format('woff'),
         url('/ot/fonts/Vazir.ttf') format('truetype');
}

html,
body {
	width: 100%;
	height: 100%;
	direction: rtl;
}

/* body,
button {
	font-family: 'Vazir' !important;
} */

.App {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: center;
	min-height: 600px !important;
	max-height: 1080px !important;
	height: 100vh !important;
	position: relative;
	
	width: 100%;
	margin: auto;
	/*border: 1px solid #000000;*/
}

@media (min-width:640px) {
	.App, .App > div{
		width:640px !important;
	}
	
}

/* #frame {
	display: flex;
	width: 640px;
	height: calc( 100% - 20px );
	border-radius: 10px;
	overflow: hidden;
} */

.App > div {
	position: absolute;
	overflow: hidden;
	height: calc( 100% - 10px );
	border-radius: 10px;
	display: flex;
	background-size: cover;
}

.hidden {
	visibility: hidden;
}


.chapter{
	font-size: 30px;
	color: white;
	text-shadow: 1px 1px 6px black;
	font-weight: bold;
}

.chapters-frame {
	flex-direction: column;
}

.fadeout {
	-webkit-animation-duration: 0.3s;
	        animation-duration: 0.3s;
	-webkit-animation-name: fadeOut;
	        animation-name: fadeOut;
}

.fadein {
	-webkit-animation-duration: 0.3s;
	        animation-duration: 0.3s;
	-webkit-animation-name: fadeIn;
	        animation-name: fadeIn;
}

.dialog-box {
	display: flex;
	justify-content: center;
	justify-items: center;
}

.btn-wrapper {
    display: flex;
    flex-direction: column;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
}

.resume-btn {
	-webkit-transform: translateY(126px);
	        transform: translateY(126px);
}
/* #start-screen {
	background-image: url('/img/levels/s1-1.jpg');
} */

#inner-screen {
	display: flex;
	height: 100%;
	width: 100%;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

#inner-screen img{
	width:50%;
	/* margin-top: -200px; */
	margin-bottom: 7%;
}
.btn:not(:disabled){
    cursor: pointer;
}

.start-btn, .btn{
    width:100%;
    padding: 10px;
    color: lightgreen;
    background-color: gray;
    opacity: 0.85;
    background-image: none;
    border-color: #343a40;
	text-shadow: 1px 3px black;
    font-weight: 800;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn {
	font-size: 30px;
}
.start-btn{
    color: lightgreen;
    background-color: gray;
	margin: 10px 0;
	font-size: 2em;
}
.start-btn.credits-btn,
.start-btn.about-btn
{
    color: lightblue;
}

.start-btn:hover{
    color: white;
    background-color: lightgreen;
    opacity: 1;
    background-image: none;
    border-color: #343a40;
    text-shadow: 1px 3px black;
    font-weight: 800;
}

.red-color.active:not(:disabled),
.red-color:hover:not(:disabled) {
    color: #fff;
    background-color: #c82333;
    border-color: #bd2130;
}

.chapter-selector {
	flex-wrap: wrap;
    justify-content: space-evenly;
	align-items: center;
	/* background-image: url('/img/levels/s1-1.jpg'); */
}

.chapter {
    font-size: 1.7em;
	width: 45%;
	height: 45%;
	border-radius: 7px;
	display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-align: center;
	opacity: 0.9;
	
	background-size: cover;
	background-position: bottom;
}
.chapter:hover{
    box-shadow: 0 0 10px lightblue;
    opacity: 1;
}
.dialog {
	/* background-image: url('/ot/img/dialog/left.png') !important; */
	/* padding: 1% 12% 18% 13%; */
	padding: 1em 3em 5em 3em !important;
	background-size: cover !important;
	background-repeat: round !important;
	font-size: 26px;
	line-height: normal;
}

.dialog-box {
	display: flex;
	justify-content: center;
	justify-items: center;
}

/* .dialog.right {
	background-image: url('/ot/img/dialog/right.png') !important;
} */

.dialog.up {
	/* background-image: url('/ot/img/dialog/up.png') !important; */
	padding: 4em 3em 1em 3em !important;
}

.dialog.up-right {
	/* background-image: url('/ot/img/dialog/up-right.png') !important; */
	padding: 4em 3em 1em 3em !important;
}

.dialog.center {
	/* background-image: url('/ot/img/dialog/center.png') !important; */
	padding: 2em 3em 2em 3em !important;
}
#score-board {
	display: flex;
	background: rgba(255, 255, 255, 0.85);
	width: 100%;
	height: 60px;
	position: absolute;
    top: 0;
}

#menu {
	padding: 10px;
	flex: 0 0 20%;
}

#menu img {
	max-width: 100%;
	height: 100%;
	cursor: pointer;
}

#stage {
	flex: 0 0 60%;
	display: flex;
	justify-content: center;
	align-items: center;
}

#score {
	display: flex;
	flex: 0 0 20%;
	flex-direction: column;
}

#score-text {
	display: flex;
	flex-basis: 100%;
	justify-content: center;
	align-items: center;
}

#score-num {
	display: flex;
	flex-basis: 100%;
	justify-content: center;
	align-items: center;
}
#game {
    display: flex;
    flex-wrap: wrap;
    overflow: hidden;
    
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid #000000;
    
    background-size: cover;
    background-color: aqua;
    /*background-repeat: no-repeat;*/
    background-repeat: round;
    /*background-position: bottom;*/
}

@media (min-height:641px) {
    #game {
        background-repeat: no-repeat;
        background-position: bottom;
    }
}
.dimmer{
    display: flex !important;
    position: absolute;
    top: 0 !important;
    left: 0 !important;
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
    padding: 1em;
    background-color: rgba(0,0,0,.3);
    opacity: 1;
    line-height: 1;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-transition: background-color .5s linear;
    transition: background-color .5s linear;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    will-change: opacity;
    z-index: 1000;
}
.modal {
    display: block;
    visibility: visible;

    width: 88%;
    min-height: 300px;
    margin: 0;
    position: absolute;

    background: #ffffffe6;
    border: none;
    box-shadow: 1px 3px 3px 0 rgba(0,0,0,.2),1px 3px 15px 2px rgba(0,0,0,.2);
    -webkit-transform-origin: 50% 25%;
    transform-origin: 50% 25%;
    flex: 0 0 auto;
    border-radius: .28571429rem;
    -webkit-user-select: text;
    -moz-user-select: text;
    -ms-user-select: text;
    user-select: text;
    will-change: top,left,margin,transform,opacity;
    
    z-index: 1001;
    padding: 5%;
}

.modal-main {
    position:fixed;
    background: white;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}
#about-page {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, .125);
    overflow: hidden;
    border: none;
    color: #fff !important;

    overflow: scroll;
}

#about-page .header-info {
    border-radius: calc(.25rem - 1px) calc(.25rem - 1px) 0 0;
    background: rgba(255, 255, 255, 0.75);
    color: #696969;
    font-weight: bold;
    text-align: center;
    /* font-size: 18px;*/
}

#about-page .body {
    align-items: flex-end !important;
    flex: 1 1 auto;
    padding: 1.25rem;
}

#about-page .team-card {
    /* width: 50%; */
    margin: 0 auto;
    color: black;
    align-items: center !important;
    display: flex;
    flex-wrap: wrap;
    /* margin-right: -15px;
    margin-left: -15px; */
}

#about-page .col {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
}

#about-page .card {
    background-position: center;
    background-size: cover;
    min-height: 800px;
    border-radius: 10px;
    overflow: hidden;
    border: none;
    min-height: auto;
    margin-bottom: 25px;
    text-align: center !important;
    background-color: white;
}

#about-page .card-img-top {
    width: 100%;
    border-top-left-radius: calc(.25rem - 1px);
    border-top-right-radius: calc(.25rem - 1px);
    vertical-align: middle;
    border-style: none;
}

#about-page .card-title {
    margin-bottom: .75rem;
    margin-bottom: .5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
}





#about-page .card-text:last-child {
    margin-bottom: 0;
    margin-top: 0;
    margin-bottom: 1rem;
}
#source-page {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, .125);
    overflow: hidden;
    border: none;
    color: #fff !important;

    overflow: scroll;
}

#source-page .header-info {
    border-radius: calc(.25rem - 1px) calc(.25rem - 1px) 0 0;
    background: rgba(255, 255, 255, 0.75);
    color: #696969;
    font-weight: bold;
    text-align: center;
    /* font-size: 18px;*/
}
